<template><section><html><head></head><body><h1>Label</h1>
<p><code v-pre="">vc-primitive-label</code> 组件用于加载标签图元，标签是场景中与视口对齐的文本， 只能作为 <code v-pre="">vc-collection-primitive-label</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>加载标签图元</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-label>
          <template v-for="(polyline, index) of polylines">
            <vc-primitive-label :position="polyline.positions[polyline.positions.length-1]" :key="'label'+index" :text="'面积: '+(polyline.area > 1000000 ? (polyline.area / 1000000).toFixed(2) + 'km²' : polyline.area.toFixed(2) + '㎡')" show-background="" :horizontal-origin="1" @click="clicked">
            </vc-primitive-label>
          </template>
        </vc-collection-primitive-label>
        <vc-collection-primitive-point @click="clicked">
          <template v-for="(polyline, index) of polylines2">
            <template v-for="(position, subIndex) of polyline.positions">
              <vc-primitive-point :position="position" :key="'point' + index + 'position' + subIndex" :pixel-size="8" @click="clicked" :disable-depth-test-distance="Number.POSITIVE_INFINITY"></vc-primitive-point>
            </template>
          </template>
        </vc-collection-primitive-point>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
