<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>backgroundColor</td>
<td>Object</td>
<td>Array</td>
<td>String</td>
</tr>
<tr>
<td>backgroundPadding</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 label 背景x、y方向偏移量。 <strong>结构: { x: number, y: number }</strong></td>
</tr>
<tr>
<td>disableDepthTestDistance</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 label 的深度检测距离。</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 label 的显示条件。. <strong>结构: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>eyeOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre="">optional</code> 指定 label 视角偏移量。 <strong>结构：{ x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>fillColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">white</code></td>
<td><code v-pre="">optional</code> 指定 label 填充颜色。</td>
</tr>
<tr>
<td>font</td>
<td>String</td>
<td><code v-pre="">'30px sans-serif'</code></td>
<td><code v-pre="">optional</code> 指定 label CSS 字体。</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 label 高度模式。<strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>horizontalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 label 水平对齐方式。<strong>CENTER: 0, LEFT: 1, RIGHT: -1</strong></td>
</tr>
<tr>
<td>id</td>
<td>*</td>
<td></td>
<td><code v-pre="">optional</code> 指定与 label 关联的信息。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object</td>
<td>Array</td>
<td>String</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 label 的轮廓宽度。</td>
</tr>
<tr>
<td>pixelOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0}</code></td>
<td><code v-pre="">optional</code> 指定 label 像素偏移量。 <strong>结构：{ x: number, y: number }</strong></td>
</tr>
<tr>
<td>pixelOffsetScaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 label 偏移量随相机距离改变的参数。<strong>结构：{ near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 label 的位置。 <strong>结构：{ lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> 指定 label 缩放比例。</td>
</tr>
<tr>
<td>scaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 label 的缩放显示参数。 <strong>结构： { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>指定 label 是否显示。</td>
</tr>
<tr>
<td>showBackground</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 指定 label 是否显示背景。</td>
</tr>
<tr>
<td>text</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 指定 label 文字，支持'\n'换行符。</td>
</tr>
<tr>
<td>totalScale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> 获取 label 的总比例，该比例是标签的比例乘以所计算的所需字体的相对大小与生成的字形大小的比例。</td>
</tr>
<tr>
<td>translucencyByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 label 透明度改变参数。 <strong>结构： { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>verticalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 label 垂直对齐方式。<strong>CENTER: 0, BOTTOM: 1, BASELINE: 2, TOP: -1</strong></td>
</tr>
<tr>
<td>labelStyle</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 label 绘制风格。<strong>FILL: 0, OUTLINE: 1, FILL_AND_OUTLINE: 2</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Label.html">Label</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该图元时触发。</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标单击该图元外部时触。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标左键双击该图元时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标在该图元上移动时触发。</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移动到该图元时触发。</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>鼠标移出该图元时触发。</td>
</tr>
</tbody>
</table>
